body {
  height: 100%;
  background: url(../../assets/images/shiny_overlay.svg) no-repeat 50% 50%;
  background-size: cover;
  overflow-x: hidden;
  padding: 2%;
}
a {
  text-decoration: none;
}

.container_home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.forms_container_img {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-right: 2%;
  margin-top: 250px;

  .working_img {
    position: absolute;
    right: -220px;
    top: -265px;
  }
}

.logo {
  position: absolute;
  left: 0px;
  top: 20px;
  margin-top: -150px;
  margin-left: -130px;
}

.title {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  margin-right: 15px;
  color: $white-color;
}

@media (max-width: 1024px) {
  .forms_container_img {
    margin-top: 300px;
    .working_img {
      right: -100px;
      top: -250px;
      margin-right: 25px;
    }
  }
}
